import React, { useState, useEffect } from 'react';
import ReactMapGL, { NavigationControl, GeolocateControl } from 'react-map-gl';
import WebMercatorViewport from '@math.gl/web-mercator';
import earthIcon from '../graphics/earth.svg';
import mapIcon from '../graphics/regmap.svg';
import pointIcon from '../graphics/point.svg';
import lineIcon from '../graphics/graph.svg';
import areaIcon from '../graphics/area.svg';
import layerIcon from '../graphics/layers.svg';
import measureIcon from '../graphics/pencil.svg';
import Points from './Points';
import Lines from './Lines';
import Areas from './Areas';
import LineLayers from './LineLayers';
import AreaLayers from './AreaLayers';
import Measures from './Measures';
import PointLayers from './PointLayers';
import PointPopup from './PointPopup';
import LinePopup from './LinePopup';
import AreaPopup from './AreaPopup';

let current = [];
let mode = 0;

const Map = ({points, setPoints, lines, setLines, areas, setAreas, showSidebar, tempMarks, setTempMarks, activeGid, setActiveGid, gids, setGids}) => {

    const seekCenter = (activeGid, points, lines, areas) => {
        let fpoints = points.filter(point => point.gid === activeGid);
        let flines = lines.filter(line => line.gid === activeGid);
        let fareas = areas.filter(area => area.gid === activeGid);
        
        if (fpoints.length + flines.length + fareas.length === 0) return [null, null];  
        flines = flines.map(fline => fline.points).flat();
        fareas = fareas.map(farea => farea.points).flat();
        let allPts = [...fpoints, ...flines, ...fareas].map(pt => pt.coordinates);
        let lats = allPts.map(pt => pt[1]);
        let longs = allPts.map(pt => pt[0]);
        let swBound = [Math.min(...longs), Math.min(...lats)];
        let neBound = [Math.max(...longs), Math.max(...lats)];
        return [swBound, neBound];
    };

    const origin = points.length ? points[points.length - 1].coordinates : [77.516233, 28.494164];  

    const mapStyle = {
        position: "absolute",
        right: "0",
        top: "0",
        height: "100%",
        width: showSidebar ? "calc(100vw - 384px)" : "100vw"
    };

    const [viewPort, setViewPort] = useState({
        latitude: origin[1],
        longitude: origin[0], 
        width: "fit",
        height: window.innerHeight,
        zoom: 14
    });

    const [showSatMap, toggleShowSatMap] = useState(false);
    const [showLayers, toggleShowLayers] = useState(true);
    const [showMeasures, toggleShowMeasures] = useState(true);
    const [drawLine, toggleDrawLine] = useState({draw: false, init: true});
    const [beaconDist, setBeaconDist] = useState(null);
    const [hoverCoords, setHoverCoords] = useState([]);
    const [marks, setMarks] = useState([]);
    const [selectedPoint, setSelectedPoint] = useState(null);
    const [selectedLine, setSelectedLine] = useState(null);
    const [selectedArea, setSelectedArea] = useState(null);

    const haverSine = (co1, co2) => {
        	
        const R = 6371e3; // metres
        const φ1 = co1[1] * Math.PI/180; // φ, λ in radians
        const φ2 = co2[1] * Math.PI/180;
        const Δφ = (co2[1] - co1[1]) * Math.PI/180;
        const Δλ = (co2[0] - co1[0]) * Math.PI/180;

        const a = Math.sin(Δφ/2) * Math.sin(Δφ/2) +
                Math.cos(φ1) * Math.cos(φ2) *
                Math.sin(Δλ/2) * Math.sin(Δλ/2);
        const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));

        const d = R * c; // in metres

        return d;
    };

    const handleMapClick = (e) => {
        if (drawLine.init) {
            toggleDrawLine({draw: true, init: false});
            return ;
        } else {
            let newCoord = e.lngLat;
            current = newCoord;
            setMarks([...marks, {
                gid: activeGid,
                coordinates: newCoord
            }])
        }
    };

    const handleMapHover = (e) => {
        setHoverCoords(e.lngLat);
        if (current && current[0]) setBeaconDist(haverSine(current, e.lngLat));
    }

    const keyDown = (e) => {
        if (e.key === "Escape") {
            if (marks.length) {
                if (mode === 0) {
                    setLines([...lines, {
                        gid: activeGid,
                        points: marks
                    }])
                } else if (mode === 1) {
                    (marks.length > 2) ? (
                        setAreas([...areas, {
                            gid: activeGid,
                            points: marks
                        }])
                    ) : (
                        alert("Specify more than 2 nodes for polygon")
                    )
                } else {
                    setPoints([...points, ...marks])
                }
            }
            toggleDrawLine({draw: false, init: true});
            current = [];
            setBeaconDist(null);
            setMarks([]);
        }
    };

    useEffect(() => {
        window.addEventListener("keydown", keyDown)
        return () => {
            window.removeEventListener("keydown", keyDown);
        }
    });

    useEffect(() => {
        let l = lines.length;
        if (l && lines[l-1].points.length === 0) {
            current = [];
            setBeaconDist(null);
        } 
    }, [lines]);

    useEffect(() => {
        setSelectedPoint(null);
    }, [activeGid, gids]);

    return (
        <div id="map" style={mapStyle}>
            <ReactMapGL
            {...viewPort}
            mapboxApiAccessToken={"pk.eyJ1IjoiZmVybWl2ZXJzZSIsImEiOiJja2Q2YzU0ejcwNWUyMnl2cTB6Z21vYXZsIn0.hlNWecQooJMCAwLdqiM-cQ"}
            mapStyle={showSatMap ? "mapbox://styles/mapbox/satellite-v9" : "mapbox://styles/fermiverse/ckd7yqq7u084t1iqj97nvbjc6"}
            onViewportChange={(viewPort) => {
                setViewPort({...viewPort, width: "fit", height: "fit"})
            }}
            onClick={drawLine.draw ? handleMapClick : null}
            onHover={drawLine.draw ? handleMapHover : null}
            minZoom={2}
            touchRotate={true}>
                <div style={{position: "absolute", right: "15px", top: "30px"}}>
                    <NavigationControl />
                </div>
                <div style={{position: "absolute", top: "135px", right: "15px"}}>
                    <GeolocateControl 
                    onViewportChange={(view) => {
                        const [swBound, neBound] = seekCenter(activeGid, points, lines, areas);
                        if (swBound !== null && neBound !== null) {
                            const newView = new WebMercatorViewport({
                                width: showSidebar ? window.innerWidth - 384 : window.innerWidth,
                                height: window.innerHeight
                            });
                            let {latitude, longitude, zoom} = newView.fitBounds([swBound, neBound], {padding: 40});
                            console.log(zoom)
                            zoom = Math.min(zoom, 13); //limits zoom level for individual points
                            setViewPort({...viewPort, latitude, longitude, zoom});
                        }
                    }}
                    />
                </div>

                {selectedPoint && !selectedLine && !selectedArea ? (
                <PointPopup selectedPoint={selectedPoint} setSelectedPoint={setSelectedPoint} 
                points={points} setPoints={setPoints} />
                ) : (null)}

                {selectedPoint && selectedLine && !selectedArea ? (
                <LinePopup selectedPoint={selectedPoint} setSelectedPoint={setSelectedPoint} 
                selectedLine={selectedLine} setSelectedLine={setSelectedLine} 
                lines={lines} setLines={setLines} />
                ) : (null)}

                {selectedPoint && selectedArea && !selectedLine ? (
                <AreaPopup selectedPoint={selectedPoint} setSelectedPoint={setSelectedPoint}
                selectedArea={selectedArea} setSelectedArea={setSelectedArea} 
                areas={areas} setAreas={setAreas} />
                ) : (null)}

                <Points points={points} setPoints={setPoints} type={""} 
                activeGid={activeGid} setSelectedPoint={setSelectedPoint} 
                setSelectedArea={setSelectedArea} setSelectedLine={setSelectedLine} />
                {marks.length ? (
                    <Points points={marks} setPoints={setMarks} type={"temp"} />
                ) : (null)}
                {tempMarks.length ? (
                    <Points points={tempMarks} setPoints={setTempMarks} type={"temp"} />
                ) : (null)}
                <Lines lines={lines} setLines={setLines} activeGid={activeGid} 
                setSelectedPoint={setSelectedPoint} setSelectedLine={setSelectedLine} setSelectedArea={setSelectedArea} />
                <Areas areas={areas} setAreas={setAreas} activeGid={activeGid} 
                setSelectedPoint={setSelectedPoint} setSelectedArea={setSelectedArea} setSelectedLine={setSelectedLine} />

                <button className="custom" id="point" title="Add a point" onClick={() => {
                    if (drawLine.draw) {
                        current = [];
                        mode = 0;
                        setBeaconDist(null);
                        setPoints([...points, ...marks]);
                        setMarks([]);
                    } else {
                        mode = 2;
                    }
                    toggleDrawLine({draw: !drawLine.draw, init: true});
                    }}>
                    <img src={pointIcon} alt="pt" width="6px"></img>
                </button>

                <button className="custom" id="line" title="Add a line" onClick={() => {
                    if (drawLine.draw) {
                        current = [];
                        setBeaconDist(null);
                        setLines([...lines, {
                            gid: activeGid,
                            points: marks
                        }]);
                        setMarks([]);
                    }
                    mode = 0;
                    toggleDrawLine({draw: !drawLine.draw, init: true});
                    }}>
                    <img src={lineIcon} alt="line" width="17px"></img>
                </button>

                <button className="custom" id="area" title="Add a polygon" onClick={() => {
                    if (drawLine.draw) {
                        current = [];
                        mode = 0;
                        setBeaconDist(null);
                        if (marks.length >= 3) {
                            setAreas([...areas, {
                                gid: activeGid,
                                points: marks
                            }]);
                        }
                        setMarks([]);
                    } else mode = 1;
                    toggleDrawLine({draw: !drawLine.draw, init: true});
                    }}>
                    <img src={areaIcon} alt="ar" width="17px"></img>
                </button>

                {drawLine.draw ? (
                    <div className="top-drawer" id={showSidebar ? "no-drop" : "drop"}>
                        <p>{`Draw Mode: ${mode === 0 ? "Line" : (mode === 1 ? "Polygon" : "Point")}, press Esc to exit`}</p>
                        {hoverCoords[0] ? (<p>{`Latitude: ${hoverCoords[1].toFixed(6)}, Longitude: ${hoverCoords[0].toFixed(6)}`}</p>) : (null)}
                        {beaconDist ? (<p>{`Displacement: ${beaconDist.toFixed(3)}m`}</p>) : (null)}
                    </div>
                ) : (null)}
               
                {showLayers ? (
                    <PointLayers points={points} activeGid={activeGid} />
                ) : (null)}

                {showLayers ? (
                    <LineLayers lines={lines} />
                ) : (null)}

                {showLayers ? (
                    <AreaLayers areas={areas} />
                ) : (null)}

                {showMeasures ? (
                    <Measures entities={lines} type={"lines"} activeGid={activeGid} />
                ) : (null)}

                {showMeasures ? (
                    <Measures entities={areas} type={"areas"} activeGid={activeGid} />
                ) : (null)}

                <button className="blank" id="measure" title={showMeasures ? "Hide measures" : "Show measures"} onClick={() => {
                    if (lines.length || areas.length) toggleShowMeasures(!showMeasures)
                    else toggleShowMeasures(false)
                    }}>
                    <img src={measureIcon} alt="Ms" width="20px"></img>
                </button>
                
                <button className="blank" id="GJ" title={showLayers ? "Hide Layers" : "Show Layers"} onClick={() => {
                    if (lines.length || areas.length) toggleShowLayers(!showLayers)
                    else toggleShowLayers(false)
                    }}>
                    <img src={layerIcon} alt="Layer" width="24px"></img>
                </button>
                
                {showSatMap ? (
                    <button className="blank" id="toggle" title="Regular View" onClick={() => {toggleShowSatMap(!showSatMap)}}>
                        <img src={mapIcon} alt="mapicon" width="24px"></img>
                    </button>
                ) : (
                    <button className="blank" id="toggle" title="Satellite View" onClick={() => {toggleShowSatMap(!showSatMap)}}>
                        <img src={earthIcon} alt="sat" width="24px"></img>
                    </button>
                )}

                <button className="rounded" id="reset" title="Reset Markers" onClick={() => {
                    const check = window.confirm("Clear all geoJSON entities and reset map?");
                    if (check) {
                        toggleDrawLine({draw: false, init: true});
                        setPoints([]);
                        setLines([]);
                        setAreas([]);
                        setActiveGid("Group 1");
                        setGids(["Group 1"]);
                        current = [];
                        setBeaconDist(null);
                        setSelectedPoint(null);
                    }
                }}>Reset</button>
            </ReactMapGL>
        </div>
    )
}

export default Map;

