import React, { PureComponent } from 'react';
import { Popup } from 'react-map-gl';
import uuid from 'react-uuid';

function convertProps(props) {
    if (props) {
        let keys = Object.keys(props);
        let mprops = [];
        if (keys.length) {
            keys.forEach(key => {
                mprops.push({
                    key: key,
                    value: props[key]
                })
            })
        }
        return mprops;
    } else return [];
}

class PointPopup extends PureComponent {
    state = { 
        metaProps: this.props.selectedPoint.properties ? convertProps(this.props.selectedPoint.properties) : []
    }
    
    componentDidUpdate(prevProps) {
        if (prevProps.selectedPoint !==this.props.selectedPoint) {
            this.setState({
                metaProps: this.props.selectedPoint.properties ? convertProps(this.props.selectedPoint.properties) : []
            })
        }
    }

    render() { 
        const {selectedPoint, setSelectedPoint, points, setPoints} = this.props;
        const {coordinates, gid} = selectedPoint;
        const index = points.indexOf(selectedPoint);
        return ( 
            <Popup 
            latitude={coordinates[1]}
            longitude={coordinates[0]}
            offsetTop={-38.71}
            closeOnClick={false}
            sortByDepth={true}
            captureScroll={true}
            onClose={(e) => {
                setSelectedPoint(null);
            }}>
                <table className="edit">
                    <thead>
                        <tr>
                            <td>Key</td>
                            <td>Value</td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Group</td>
                            <td>{gid}</td>
                        </tr>
                        <tr>
                            <td>Entity</td>
                            <td>Point</td>
                        </tr>
                        <tr>
                            <td>Latitude</td>
                            <td style={{color: "blue"}}>{coordinates[1]}</td>
                        </tr>
                        <tr>
                            <td>Longitude</td>
                            <td style={{color: "blue"}}>{coordinates[0]}</td>
                        </tr>
                        {this.state.metaProps.map(mprop => (
                            <tr key={uuid()}>
                                <td id={`meta_${this.state.metaProps.indexOf(mprop)}`} contentEditable="true" suppressContentEditableWarning="true" onBlur={(e) => {
                                    mprop["key"] = document.getElementById(`meta_${this.state.metaProps.indexOf(mprop)}`).innerText;
                                }}>{mprop["key"]}</td>
                                <td id={`val_${this.state.metaProps.indexOf(mprop)}`} contentEditable="true" suppressContentEditableWarning="true" onBlur={(e) => {
                                    mprop["value"] = document.getElementById(`val_${this.state.metaProps.indexOf(mprop)}`).innerText;
                                }}>{mprop["value"]}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
                <div id="button-set" style={{display: "flex", alignItems: "baseline", justifyContent: "space-between"}}>
                    <button className="rounded" id="fAdd" title="Add a field" onClick={() => {
                        this.setState({metaProps: [...this.state.metaProps, {
                            key: "",
                            value: ""
                        }]})
                    }}>Add</button>
                    <button className="rounded" id="fReset" title="Clear meta properties" onClick={() => {
                        this.setState({metaProps: []})
                    }}>Clear</button>
                    <button className="rounded" title="Confirm" onClick={() => {
                        let modPoints = [...points];
                        let modProps = {};
                        for (let prop of this.state.metaProps) {
                            if (prop["key"]) modProps[prop["key"]] = prop["value"]
                        }
                        modPoints.splice(index, 1, {...selectedPoint, properties: modProps});
                        setPoints(modPoints);
                        setSelectedPoint(null);
                    }}>Apply</button>
                </div>
            </Popup>
        );
    }
}
 
export default PointPopup;
